/**
 * Voice Commands Component
 * Voice commands integration for hands-free data queries
 */

import React, { useState, useEffect, useRef } from "react";
import { Card, Button, Space, Typography, Tag, Alert, message } from "antd";
import {
  AudioOutlined,
  StopOutlined,
  SoundOutlined,
  ReloadOutlined,
} from "@ant-design/icons";
import "./VoiceCommands.css";

const { Title, Text } = Typography;

const VoiceCommands = ({ onCommand = null }) => {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState("");
  const [isSupported, setIsSupported] = useState(false);
  const [error, setError] = useState(null);
  const recognitionRef = useRef(null);

  useEffect(() => {
    // Check browser support
    if ("webkitSpeechRecognition" in window || "SpeechRecognition" in window) {
      setIsSupported(true);
      initializeSpeechRecognition();
    } else {
      setIsSupported(false);
      setError("Voice commands kh√¥ng ƒë∆∞·ª£c h·ªó tr·ª£ trong tr√¨nh duy·ªát n√†y.");
    }

    return () => {
      if (recognitionRef.current) {
        recognitionRef.current.stop();
      }
    };
  }, []);

  const initializeSpeechRecognition = () => {
    try {
      const SpeechRecognition =
        window.SpeechRecognition || window.webkitSpeechRecognition;
      const recognition = new SpeechRecognition();

      recognition.continuous = false;
      recognition.interimResults = false;
      recognition.lang = "vi-VN"; // Vietnamese

      recognition.onstart = () => {
        setIsListening(true);
        setError(null);
        message.info("ƒêang nghe...");
      };

      recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        setTranscript(transcript);

        if (onCommand) {
          onCommand(transcript);
        }

        message.success(`ƒê√£ nh·∫≠n l·ªánh: ${transcript}`);
      };

      recognition.onerror = (event) => {
        console.error("Speech recognition error:", event.error);
        setError(`L·ªói nh·∫≠n di·ªán gi·ªçng n√≥i: ${event.error}`);
        setIsListening(false);
        message.error("L·ªói nh·∫≠n di·ªán gi·ªçng n√≥i");
      };

      recognition.onend = () => {
        setIsListening(false);
      };

      recognitionRef.current = recognition;
    } catch (err) {
      console.error("Speech recognition initialization error:", err);
      setError("Kh√¥ng th·ªÉ kh·ªüi t·∫°o voice recognition");
      setIsSupported(false);
    }
  };

  const startListening = () => {
    if (!isSupported) {
      message.warning("Voice commands kh√¥ng ƒë∆∞·ª£c h·ªó tr·ª£");
      return;
    }

    if (recognitionRef.current) {
      try {
        recognitionRef.current.start();
      } catch (err) {
        console.error("Start listening error:", err);
        message.error("Kh√¥ng th·ªÉ b·∫Øt ƒë·∫ßu nghe");
      }
    }
  };

  const stopListening = () => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      setIsListening(false);
    }
  };

  return (
    <Card className="voice-commands">
      <Title level={4}>
        <SoundOutlined /> Voice Commands
      </Title>

      {!isSupported && (
        <Alert
          message="Kh√¥ng h·ªó tr·ª£"
          description="Tr√¨nh duy·ªát c·ªßa b·∫°n kh√¥ng h·ªó tr·ª£ voice recognition. Vui l√≤ng s·ª≠ d·ª•ng Chrome ho·∫∑c Edge."
          type="warning"
          showIcon
          style={{ marginBottom: 16 }}
        />
      )}

      {error && (
        <Alert
          message="L·ªói"
          description={error}
          type="error"
          showIcon
          closable
          onClose={() => setError(null)}
          style={{ marginBottom: 16 }}
        />
      )}

      <Space direction="vertical" style={{ width: "100%" }} size="large">
        <div className="voice-controls">
          {!isListening ? (
            <Button
              type="primary"
              size="large"
              icon={<AudioOutlined />}
              onClick={startListening}
              disabled={!isSupported}
              style={{ width: "100%" }}
            >
              B·∫Øt ƒë·∫ßu nghe
            </Button>
          ) : (
            <Button
              danger
              size="large"
              icon={<StopOutlined />}
              onClick={stopListening}
              style={{ width: "100%" }}
            >
              D·ª´ng nghe
            </Button>
          )}
        </div>

        {transcript && (
          <Card size="small" title="L·ªánh ƒë√£ nh·∫≠n">
            <Text>{transcript}</Text>
          </Card>
        )}

        <div className="voice-tips">
          <Text strong>G·ª£i √Ω l·ªánh:</Text>
          <Space wrap style={{ marginTop: 8 }}>
            <Tag>Show me trends</Tag>
            <Tag>Find anomalies</Tag>
            <Tag>Summary data</Tag>
            <Tag>Filter by date</Tag>
            <Tag>Search for...</Tag>
          </Space>
        </div>
      </Space>
    </Card>
  );
};

export default VoiceCommands;
